<template>
   
    <div class="col-md-12 pb-2 px-0" id="dsfileList">
        <loader :is-visible="isLoading"></loader>
        <div class="theme-primary partition p-actions-expand tablehead p-actions-collapse mb-3">
            <span class="float-left p-name text-white">Tag</span>
           
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12  text-right p-0 theme-primary">
                <span class="p-actions float-right">
                    <a href="javascript:;" id="aAddTag" @click="OpenTagPopUp" title="AddTag"
                        class="p-action-btn text-white">
                        <em class="fa fa-plus"></em> <br />{{ $t('AddTag') }}
                    </a>
                    <a id="aActive" href="javascript:;" @click="UpdateStatus('1001')" class="p-action-btn text-white disabled"
                        title="" data-toggle-tooltip="tooltip" data-original-title="Add">
                        <em class="fa fa-check  action-icon"></em> <br>{{ $t("Active") }}
                    </a>
                    <a href="javascript:;" id="aInactive" title="Share" class="p-action-btn text-white disabled"
                        @click="UpdateStatus('1002')">
                        <em class="fa fa-close"></em> <br />{{ $t('Inactive') }}
                    </a>

                    <a href="javascript:;" id="aDeleteAll" @click="CommonDelete" title="Delete"
                        class="p-action-btn text-white disabled">
                        <em class="fa fa-trash"></em> <br />{{ $t('Delete') }}
                    </a>

                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em> </a>
                </span>

            </div>

        </div>
        <div class="row mt-1">
            <div class="col-6 float-left pr-1 pl-3">
                <div class="form-group">
                    <input id="search" maxlength="50" class="form-control search" v-model="searchTxt"   type="text" placeholder="Search By Tag Name" >
                </div>
            </div>
            <div class="col-5 float-left pl-0">
                <div class="background-0 text-left form-check-inline">
                    <input type="hidden" id="hdnSearch" value="">
                    <a title="" id="Go" href="javascript:;" class="btn btn-success mr-1 px-4 hlsearch" @click="FetchData()" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a>
                    <a href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" @click="ResetListing()" data-toggle-tooltip="tooltip" data-original-title="Clear all "><i class="fa fa-refresh"></i></a>
                </div>
            </div>
        </div>
        <div class="listing">

            <div class="table-responsive table-fix-header" id="div_forListView">
                <table class="table table-bordered mt-0" style="width: 100%;" id="tblTagList">
                    <thead class="thead-bg">
                        <tr>
                            <th style="width:50px !important;" v-on:change="onChange()" class="text-center"><input
                                    id="chkAll" class="chkItems" value="" type="checkbox"></th>
                            <th class="text-center" @click="SortData($event, 'NAME', 'Sort')">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'NAME', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'NAME' }">Tag
                                    Name</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'DESCRIPTION', 'Sort')">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'DESCRIPTION', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'DESCRIPTION' }">Description</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'STATUS', 'Sort')">
                                <span>Status By</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'Edit', 'Sort')">
                                <span>Edit</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr v-for="item in TagListing" :key="item">
                            <td class="text-center"><input :value="item.TAG_ID" 
                                    v-on:change="onChange()" class="chkItems" type="checkbox"></td>
                            <td class="text-center">
                                <i class="fa fa-tag" aria-hidden="true"></i> &nbsp;
                                <span >
                                    {{item.TAG_NAME}}</span>
                            </td>
                            <td class="text-center">
                                <span >
                                    {{ item.TAG_DESC }}</span> 
                            </td>
                            <td class="text-center">
                                <div class="cstm-drop-btn">
                                    <select  v-if="item.STATUS_ID == '1001'"  v-on:change="DrpdownUpdataStatus(item.TAG_ID)"  :id="'STATUS_IDs'+item.TAG_ID" :data-initial-value="item.STATUS_ID" :class="{ 'btn-success': item.STATUS_ID == '1001', 'btn-danger': item.STATUS_ID == '1002' }" >    
                                         <option value="1001" selected="selected">Active</option>
                                         <option value="1002">Inactive</option>
                                    </select>
                                         <select  v-if="item.STATUS_ID == '1002'"  v-on:change="DrpdownUpdataStatus(item.TAG_ID)" :id="'STATUS_IDs'+item.TAG_ID" :data-initial-value="item.STATUS_ID" :class="{ 'btn-success': item.STATUS_ID == '1001', 'btn-danger': item.STATUS_ID == '1002' }" >
                                         <option value="1001" >Active</option>
                                         <option value="1002" selected="selected">Inactive</option>
                                    </select>
                                </div>
                            </td>
                            <td class="text-center ">
                                <a href="javascript:;" class="update text-info action-link" id="ancEditPriority" :value="item.TAG_ID" @click="EditTags(item.TAG_ID)" typetitle="Edit" typefor="Edit" title="Edit" >
                                    <em class="fa fa-pencil text-success action-icon"></em>
                                </a>
                            </td>
                        </tr>
                        <tr>
                            <td v-if="NoRecordLength == 0 && TagListing.length == 0" colspan="7" class="text-danger text-center no-record">No
                                Record found</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev" v-on:change="pageSizecommon" :pageNo="PageNumber"
                first-text="First" last-text="Last" next-text="Next" v-on:click="pagerMethod"></Pager>
        </div>
        <ModalAddTagPopup @closeAddTagPopup="closeAddTagPopup" :TagData="TagData" :isDivActiveFromTagListing="isDivActiveFromTagListing" :isDivActive="isDivActive"  @BindTagData="BindTagData" v-if="isShowTagPopup">
    </ModalAddTagPopup>
    </div>
    
</template>
<script>
import Pager from '../../components/Common/Pager.vue';
import DataService from '../../services/DataService';
import ModalAddTagPopup from "../UI/Modal/ModalSaveTagPopup.vue";
export default {
    components:
    {
        ModalAddTagPopup,
        Pager
    },

    data() {
        return {
            isLoading: false,
            loggeduserid: null, //Get logged in user info
            companyId: null, // Get company id
            TagListing: [],
            NoRecordLength: null,
            isShowTagPopup: false,
            totalRecords:null,
            PageNumber:1,
            isDivActive:false,
            isDivActiveFromTagListing:false,
            TagData:[],
            searchCondition: null,
            pageSize:10,
            PageNo: 1,
            listCode:null,
            sortExp: null,
            sortBy:null,
            TotalPages:0,
            searchTxt:''
        }
    },
    created: function () {
        
        this.loggeduserid = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        var vm = this;
         vm.FetchData();
        
    },
    methods: {
        EditTags:function(id)
        {            
            var vm = this;
            vm.isDivActiveFromTagListing=true;
            vm.isDivActive=false;
            var params= `tagid=${id}&CompanyId=${vm.companyId}&sourcecode=`;
            DataService.gettalygentagdetail(params).then(function (response) {                
                vm.TagData=response.data;
                vm.isShowTagPopup=true;
            });
        },
        pagerMethod: function (e) {
            var vm =  this;
            if (e.target.innerHTML == 'Prev') {
                this.PageNo = this.PageNo - 1;
            }
            else if (e.target.innerHTML == 'Next') {
                this.PageNo = parseInt(this.PageNo) + 1;
            }
            else {
                this.PageNo = e.target.innerHTML;
            }
            if (this.PageNo == "First") {
                this.PageNo = "1";
            }
            if (this.PageNo == "Last") {
                this.PageNo = vm.TotalPages
            }
            if (parseInt(this.PageNo) > 0) {
               vm.PagerData(vm.PageNo,vm.pageSize)
            }
            else {
                this.PageNo = 1
            }
        },
        pageSizecommon: function (e) {
            var vm = this;
            var scondition = ""
            if (vm.searchCondition == null) {
                scondition = vm.searchCondition
            }
            else {
                scondition = vm.searchCondition.trim();
            }
            vm.pageSize = e.target.value
           vm.PagerData(vm.PageNo, vm.pageSize)
        },

        SortData: function (ev, sortBy, className, name) {
            var vm = this;
            var clases = ev.target.getAttribute('class');
             vm.sortBy = sortBy;
            if (clases == 'sort' || clases == 'sort tb_headerSortUp') {
                vm.sortExp = 'ASC'
            }
            if (clases == 'sort tb_headerSortDown') {
                vm.sortExp = 'DESC'
            }
           
            vm.FetchData(vm.PageNo,vm.sortBy,vm.sortExp)
            
        },


        PagerData: function (pagesize, pagecountlist) {
            var vm = this;
            vm.PageNo = pagesize;          
            vm.pageSize = pagecountlist == undefined ? vm.pageSize : pagecountlist;     
            vm.FetchData(vm.PageNo,vm.pageSize,vm.sortExp);     
        },

        DrpdownUpdataStatus: function(id)
        {
            var vm = this;
            var statusCode= $('#STATUS_IDs'+id).find(":selected").val();
            vm.isLoading = true;
            var params = `statusId=${statusCode}&tablename=TALYGEN_tag&primaryfield=tag_id&recordIds=${id}`;
            DataService.UpdateCommonStatus(params).then(function (response) {
                if(response.data==0)
                {
                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                    $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                    vm.ShowAlert([vm.$t('TagUpdateSuccess')], "success", true, vm.$t("Alert"));
                    vm.FetchData();
                }
                else {
                        vm.ShowAlert(vm.$t("UpdateError", [vm.$t('Tag')]), "success", true, vm.$t("Alert"));
                    }
                vm.isLoading = false;
            })
            setTimeout(function () { vm.ResponsiveDataTable('div_forListView') }, 500); 
        },
        closeAddTagPopup: function()
        {
            var vm = this;
            vm.isShowTagPopup=false;
        },
        OpenTagPopUp: function () {
            var vm = this;
            vm.isDivActiveFromTagListing=true;
            vm.isDivActive=false;
            vm.TagData=[];
            vm.isShowTagPopup=true;
        },
        // Common Delete method 
        CommonDelete: function () {
            var vm = this;
            var val = "";
            $('#div_forListView .chkItems:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            var str = "";
            if (val.length > 0 || id != null) {
                vm.isLoading=true;
                vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                    if (val != null) {
                        str += val + ',';
                    }
                   
                let params = {};
                params["ids"] = str;
                params["refCode"] = "Doc_Tag";
                params["companyId"] = vm.loggedcompanyid;
                params["userId"] = vm.loggeduserid;
                params["moduleName"] = "DocStorage";
                DataService.CommonDelete(params).then(function (response) {
                    vm.isLoading = true;
                    if (response.data[0].Status == "Success") {
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                         vm.ShowAlert([vm.$t('TagDeleted')], "success", true, vm.$t("Alert"));
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("DeletedError", [vm.$t('Folder')]), "success", true, vm.$t("Alert"));
                    }
                    });
                });
                vm.isLoading=false;
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                vm.isLoading=false;
            }
        },
         onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#aInactive').removeClass('disabled');
                $('#aActive').removeClass('disabled');
                $('#aDeleteAll').removeClass('disabled');
                

            }
            else {
                $('#aInactive').addClass('disabled');
                $('#aActive').addClass('disabled');
                $('#aDeleteAll').addClass('disabled');
               
            }
        },
        UpdateStatus: function (statusCode) {
            var vm = this;
            vm.isLoading = true;
            var val = "";
            $('#div_forListView .chkItems:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            var params = `statusId=${statusCode}&tablename=TALYGEN_tag&primaryfield=tag_id&recordIds=${val}`;
            DataService.UpdateCommonStatus(params).then(function (response) {
                if(response.data==0)
                {
                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                    $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                    vm.ShowAlert([vm.$t('TagUpdateSuccess')], "success", true, vm.$t("Alert"));
                    vm.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("UpdateError", [vm.$t('Tag')]), "success", true, vm.$t("Alert"));
                    }
                vm.isLoading = false;

            })
            setTimeout(function () { vm.ResponsiveDataTable('div_forListView') }, 500);
        },
        ResetListing: function()
        {
            var vm=this;
            vm.searchTxt='';
            vm.PageNo=1;
            vm.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            vm.TagListing = [];
             $('#aInactive').addClass('disabled');
             $('#aActive').addClass('disabled');
             $('#aDeleteAll').addClass('disabled');
             $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
            var params = `pageNo=${vm.PageNo}&SearchFilter=${vm.searchTxt}&pageSize=${vm.pageSize}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&modulename=DocStorage`;
            DataService.GetTagListByModule(params).then(function (response) {
                vm.TagListing = response.data.data;
                vm.totalRecords = response.data.pager.totalItems;
                vm.TotalPages = Math.ceil(vm.totalRecords / vm.pageSize);
                if (vm.TagListing.length == 0) {
                    vm.NoRecordLength = 0;
                }
                vm.isLoading = false;
            })
            setTimeout(function () { vm.ResponsiveDataTable('div_forListView') }, 500);
        },

    },
}
</script>