<script>
import AddTagDataService from "@/services/DataService";
export default {
  props:
  {
    isDivActiveFromTagListing: {
      type: Boolean
    },
    isDivActive: {
      type: Boolean
    },
    TagData: {
      type: Array,
      required: true,
    }
  },
  data() {
    return {
      isLoading: false,
      tagname: "",
      tagId: "",
      arrayTagData: [],
      TagsArray: [],
      selectedStatus: '',
      tagDescription: '',
      TagIds: 0
    };
  },
  updated: function () {
    this.BindActionButtonEvent();
  },
  created: function () {   
    var vm = this;
    vm.GetTags();
    if (vm.TagData != undefined) {
      if (vm.TagData.tag_id != null) {
        vm.BindTagData(vm.TagData);
      }
    }

  },
  methods: {
    BindTagData: function (TagData) {
      var vm = this;
      if (vm.TagData != undefined) {
        vm.tagname = TagData.tag_name,
          vm.tagDescription = TagData.tag_desc
        vm.selectedStatus = TagData.status_id.toString();
        vm.TagIds = TagData.tag_id;
      }

    },

    GetTags: function () {
      var vm = this;
      let params = {};
      params["type"] = "DOC_TAG";
      var param = "moduleName=DocStorage&type=DOC_TAG"
      AddTagDataService.GetDDLDatas(param).then((response) => {
        vm.arrayTagData = response.data.DATA;
        setTimeout(function () {
          vm.ResponsiveDataTable('tblRecycleBinFileList')
        }, 500);
        setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
      })
    },
    DeleteTag: function (id) {
      var vm = this;
      vm.confirmR("Are you sure you want to delete this record ?", true, (vm.$t("Warning") + "  " + vm.$t("Doc Storage Source")), function () {
        AddTagDataService.DeleteTag(id).then((response) => {          
          vm.arrayTagData = response.data.DATA;
          vm.GetTags();
          setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
        })
      })
    },
    /*

    Method for  save new tag names.
    
    */
    SaveTag: function () {      
      var vm = this;
      vm.isLoading = true;
      vm.$refs.form.validate().then((result) => { 
        debugger   
        if (result) {       
            var jsonobj = {
              tagName: vm.tagname.toLowerCase(),
              tagDesc: vm.tagDescription,
              tagStatusId: vm.selectedStatus == '' ? 1001 : vm.selectedStatus,
              TagId: vm.TagIds == undefined ? 0 : vm.TagIds,
              moduleName: 'DocStorage'
            };
            var objectPostString = JSON.stringify(jsonobj);
            AddTagDataService.SaveTag(objectPostString).then(function (response) {              
              if (response.data[0].Value > 0) {
                vm.ShowAlert(vm.$t('TagAddedSuccessfully', [vm.$t('Added')]), "success", true, vm.$t("Alert"));
                vm.GetTags();
                vm.tagname='';
                vm.$parent.FetchData();
                vm.$emit("closeAddTagPopup");
                //vm.$parent.AssignTag();
              }
              else if (response.data[0].Value == 0) {
                vm.ShowAlert(vm.$t('TagAddedSuccessfully', [vm.$t('Updated')]), "success", true, vm.$t("Alert"));
                vm.$parent.FetchData();
                vm.$emit("closeAddTagPopup");
              }
              else if (response.data[0].Status == "Exist") {
                vm.ShowAlert(vm.$t('TagAlreadyExist'), "failure", true, vm.$t('Alert'));
              }
              else {
                vm.$emit("closeAddTagPopup");
              }
            });
          }
          // else {
          //   vm.ShowAlert(vm.$t('TagNameIsRequired'), "failure", true, vm.$t('Alert'));
          // }  
        vm.isLoading = false;
      });
    },
  },
};
</script>
<template>
  
  <!-- Tag add/edit from manage tag listing page -->
  <div class="my-popups">
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header theme-primary partition-full">
            <h4 class="modal-title">{{ $t('Tag') }} {{ $t('Name') }}</h4>
            <button type="button" class="close" v-on:click="$emit('closeAddTagPopup')">
              <em class="fa fa-times" aria-hidden="true"></em>
            </button>
          </div>
          <div class="modal-body" v-if="isDivActiveFromTagListing==false">
            <Form ref="form" v-slot="{ valid, errors }">
              <div class="col-lg-12 float-left px-0">
                <div class="row" style="max-height: 450px;">
                  <div class="col-lg-12">
                    <div class="form-group">
                      <label>Tag Name :</label><span class="text-danger">*</span>
                      <div  style="position: relative;">
                        <Field name="TagName" :rules="{ 'required': true }" label="Title" v-slot="{ errors }"
                          v-model="tagname">
                          <input name="TagName" type="text"
                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" v-model="tagname"
                            maxlength="50" />
                          <span name="TagName" v-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                        </Field>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="listing">
                  <div class="table-responsive table-fix-header" id="div_forListView">
                    <table class="table table-bordered mt-0" style="width: 100%;" id="tblRecycleBinFileList">
                      <thead class="thead-bg">
                        <tr>
                          <th class="text-center">{{ $t('TagName') }}</th>
                          <th class="text-center">{{ $t('Action') }}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="item in arrayTagData">
                          <td class="text-center">{{ item.name }}</td>
                          <td class="text-center"><em class="fa fa-trash font-20 text-danger" :value="item.value"
                              @click="DeleteTag(item.value)"></em></td>
                        </tr>
                        <tr v-if="arrayTagData.length == 0">
                          <td class="text-center text-danger no-record" colspan="4">
                            <span>{{ $t('NoRecordfound') }}</span>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <div class="row flex-row-reverse">
                  <div class="col-lg-6 text-right mt-2">
                    <a href="javascript:;" v-on:click="SaveTag(valid, errors);" title="Save"
                          class="btn btn-success save-btn mr-1">
                          <em class="fa fa-save pr-2"></em>
                          {{$t('Save')}}
                    </a>
                    <a href="javascript:;" v-on:click=" $emit('closeAddTagPopup') " class="btn btn-danger  delete-btn">
                          <em class="fa fa-times"></em>
                          {{$t('Cancel')}}
                    </a>
                  </div>
                  <div class="col-lg-6">
                    <div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small></div>
                  </div>
              </div>
            </Form>
          </div>
          <div class="modal-body" v-if="isDivActiveFromTagListing">
            <Form v-slot="{ valid, errors }" ref="form">
              <div class="col-md-12 float-left px-0">
                <div class="row" style="max-height: 450px;">
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label for="TagName">{{ $t('TagName') }}</label><span class="text-danger">*</span>
                      <Field name="TagName" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                        <input name="TagName" type="text"
                          v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }" v-model="tagname" />
                        <span v-if="errors[0]" name="TagName" class="invalid-feedback">{{ errors[0] }}</span>
                      </Field>
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label for="Status">{{ $t('Status') }}</label><span class="text-danger">*</span>
                      <Field name="selectedStatus" v-model="selectedStatus" :rules="{ 'required': true }"
                        label="Status Name" v-slot="{ errors }">
                        <select  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                          v-model="selectedStatus" placeholder="Select">
                          <option value="" selected="Selected">Select</option>
                          <option value="1001">Active</option>
                          <option value="1002">Inactive</option>
                        </select>
                        <span name="selectedStatus" v-if="errors[0]"
                          class="invalid-feedback d-block">{{ errors[0] }}</span>
                      </Field>
                    </div>
                  </div>
                  <div class="col-lg-12">
                    <div class="form-group">
                      <label for="Description">Description:</label>
                      <Field class="w-100" name="Description" :rules="{ 'required': false, 'max': 500 }"
                        v-slot="{ errors }">
                        <textarea cols="5" rows="5" v-model="tagDescription"
                          v-bind:class="{ 'form-control w-100': true }" />
                        <span v-if="errors[0]" name="Description" class="invalid-feedback">{{ errors[0] }}</span>
                      </Field>
                    </div>
                  </div>                         
                </div>
              </div>
              <div class="row flex-row-reverse">
                  <div class="col-lg-6 text-right">
                    <a href="javascript:;" v-on:click="SaveTag(valid, errors);" title="Save"
                          class="btn btn-success save-btn mr-1">
                          <em class="fa fa-save pr-2"></em>
                          {{$t('Save')}}
                    </a>
                    <a href="javascript:;" v-on:click=" $emit('closeAddTagPopup') " class="btn btn-danger  delete-btn">
                          <em class="fa fa-times"></em>
                          {{$t('Cancel')}}
                    </a>
                  </div>
                  <div class="col-lg-6">
                    <div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small></div>
                  </div>
              </div>
            </Form>        
          </div>         
        </div>
      </div>
    </div>
  </div>
</template>